.work-item-container {
    margin: 2em 0;
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;

    h1 {
        color: $primary-red;
        font-weight: 400;
        
        a {
            text-decoration: none;
            color: $primary-red;
        }
    }

    p {
        margin-left: 3em;
    }

    .work-info {
        line-height: 1.6;
    }

    .job-desc {
        margin-left: 2em;
    }

    .clickable {
        color: $primary-red;
        cursor: pointer;
    }
}

@media only screen and (max-width: 1250px) {
    .work-item-container {
        h1 {
            color: $primary-red;
            font-weight: 400;
        }

        p {
            margin-left: 1em;
        }

        .job-desc {
            margin-left: 1em;
        }
    }
 }